.menu{
    display: flex;
    flex-direction: column;
    gap:20px
}
.menu h1{
color: #401e22ea;
font-weight: 500;
}
.menu-text{
    max-width: 60%;
    columns: #808080;
}
.menu-list{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    text-align: center;
    margin: 20px 0px;
    overflow: scroll;


}
.menu-list::-webkit-scrollbar{
    display: none;
}
.menu-list-item img{
    width:7.5vw;
    min-width:80px;
    cursor:pointer;
    border-radius: 50%;
    transition: 0.2s;
}
.menu-list-item p{
    margin-top: 10px;
    color:#747474;
    font-size:max(1.4vw,6px);
    cursor: pointer;
}
.menu hr{
    margin: 10px 0px;
    height: 2px;
    background-color: #e2e2e2;
    border: none;
}
.menu-list-item .active{
    border: 4px solid tomato;
    padding: 2px;
}
@media(max-width:1050px){
    .menu-text{
        max-width: 100%;
        font-size:14px;

    }
}