.login{
    position: absolute;
    z-index:1;
    width:100%;
    height:100%;
    background-color: #00000090;
    display: grid;

}
.container{
    place-self: center;
    width: max();
    color:#808080 ;
    background-color: white;
    display: flex;
    flex-direction: column;
    gap:25px;
    padding: 25px 30px;
    border-radius: 8px;
    font-size: 14px;
    animation: fadeIn 0.5s;
}
.tittle{
    display: flex;
    justify-content: space-between;
    color: black;
    align-items: center;
}
.tittle img{
    width: 16px;
    cursor: pointer;
}
.input{
    display: flex;
    flex-direction: column;
    gap:20px;
}
.input input{
    outline: none;
    border: 1px solid #c9c9c9;
    padding: 10px;
    border-radius: 4px;
}
.container button{
    border: none;
    padding: 10px;
    border-radius: 4px;
    color:white;
    background-color: tomato;
    font-size: 15px;
    cursor: pointer;
}
.condition{
    display: flex;
    align-items: start;
    gap:8px;
    margin-top: -15px;
}
.condition input{
    margin-top: 5px;
}
.login p span{
    color: tomato;
    font-weight: 500;
    cursor: pointer;

}