.cart{
    margin:100px;

}
.cart-tittle{
    display: grid;
    grid-template-columns: 1fr 1.5fr 1fr 1fr 1fr 0.5fr;
    align-items: center;
    color: grey;
    font-size: max(1vw,12px);
}
.cart-list{
    margin: 10px 0px;
    color: black;
}
.cart-items img{
    width:50px;
}
.cross{
    cursor: pointer;
}
.bottom{
    margin-top: 80px;
    display: flex;
    justify-content: space-between;
    gap:max(12vw,20px)
}
.details{
    display: flex;
    justify-content: space-between;
    color: #555;
}
.cart-total hr{
    margin: 10px 0px;
}
.cart-total button{
    border: none;
    color: white;
    background-color: tomato;
    width: max(15vw,200px);
    padding: 12px 0px;
    border-radius: 4px;
    cursor: pointer;
}
.prmocode{
    flex:1;

}
.prmocode p{
    color: #555;

}
.cart-input{
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #eaeaea;
    border-radius: 4px;

}
.cart-input input{
    background: transparent;
    border: none;
    outline: none;
    padding: 10px;
}
.cart-input button{
    width: max(10vw,150px);
    padding: 12px 5px;
    background-color: black;
    border: none;
    color: white;
    border-radius: 4px;
}
@media (max-width:750px) {

.bottom{
    flex-direction: column-reverse;
}
.prmocode{
    justify-content: start;
    
}
}