.order{
    display: flex;
    align-items: start;
    justify-content: space-between;
    gap: 50px;
    margin-top: 100px;
}
.order-left{
    width: 100%;
    max-width: max(30%,500px);
}
.order-left .tittle{
    font-size:30px;
    font-weight: 600;
    margin-bottom: 50px;
}
.order-left input{
    margin-bottom: 15px;
    width: 100%;
    padding: 10px;
    border: 1px solid #c5c5c5;
    outline-color: tomato;
}
.order-left .multi-fields{
display: flex;
gap: 10px;
}
.order-right{
    width: 100%;
    max-width: max(40%,500px);

}
.order .cart-total button{
    margin-top: 30px;
}